                  </b-col>
                  <b-col class="col-12 col-lg-6 text-white text-right">
                    <vote21-fire :delta="local_seat_data.poll.encounter" :max="6"></vote21-fire>
                  </b-col>
                  <b-col class="col-12 text-right">
                    <vote21-ratio-bar
                      :party-list="partyList"
                      :data="local_seat_data.poll.data"
                    ></vote21-ratio-bar>
                    <small>
                    </small>
                  </b-col>
                </b-row>
              </b-col>


              <template v-if="!local_seat_data.final && !local_seat_data.exitpoll">
