                        </template>
                      </b-col>
                      <!-- <b-col class="col-12 col-md-7 text-right">
                      </b-col> -->
                      <b-col class="col-12 text-right">
                        <vote21-ratio-bar
                          :party-list="partyList"
                          :data="poll.data"
                        ></vote21-ratio-bar>
                        <small>
                        </small>
                      </b-col>
                    </b-row>
                    <b-row class="mb-2 d-none d-md-block">
