                           : {{ poll.period }}
                          <!-- , {{ poll.respondent | digit }} -->
                          , <a href="#" @click.stop.prevent="goLink(poll.link)"></a>
                        </small>
                        <br />
                        <small class="d-none d-lg-block">{{ poll.desc }}</small>
                      </b-col>
                    </b-row>
                  </b-col>
                </template>
