      this.selectProportionalSeatPoll(this.vote_result.FINAL.proportionalSeat, true);
      this.selectLocalSeatSample(this.vote_result.FINAL.localSeat)
    } else {
      this.selectProportionalSeatPoll(this.summarized_proportional_seat_poll, true);
      this.selectLocalSeatSample(this.summarized_local_seat);
    }
  },

  // http://watch.peoplepower21.org/election/
  methods: {
    showToast(title, message, variant) {
        this.$bvToast.toast(message, {
          title: title,
          variant: variant || "info",
          toaster: "b-toaster-top-center",
          solid: true
        });
    },

    clazz(type, extra) {
      const ret = [];
      if(type === 1) {
        ret.push(...["col-12", "col-sm-6", "col-md-4", "col-lg-3", "col-xl-3"]);
      } else if(type === 2) {
        ret.push(...["col-6", "col-sm-3", "col-md-4", "col-lg-2", "col-xl-2"]);
      } else if(type === 3) {
        ret.push(...["col-3", "col-sm-3", "col-md-3", "col-lg-1", "col-xl-1"]);
      } else if(type === 4) {
        ret.push(...["col-3", "col-sm-3", "col-md-3", "col-lg-2", "col-xl-2"]);
      }
      if(extra) {
        ret.push(...extra);
      }
      return ret;
    },

    makeOpacity(value) {
        // console.log(value);
      if(value) {
        // console.log(value);
        return {opacity : 0.5};
      }
      // return {opacity : 1};
    },

    selectVoteResult(poll) {
      if(poll) {
        this.selectProportionalSeatPoll(poll.proportionalSeat, true);
        this.selectLocalSeatSample(poll.localSeat)
      } else {
        this.showToast("  ", "   /    .", "danger");
      }
    },

    extendPartyInfo(item) {
      if(item && item.party in this.partyList) {
        item.color = this.partyList[item.party].color;
        item.partyName = this.partyList[item.party].name;
      }
      return item;
    },

    summaryLocalSeatPoll() {
      //  
      const local_seat_poll_summary = {};
      const local_seat_exitpoll_summary = {};

      let total = 0;
      
      const summarized_local_seat = {};
      const merged_local_seat_data = {};

      const summarized_local_state = {};
      summarized_local_state.encounter = 0;
      summarized_local_state.veryencounter = 0;
      summarized_local_state.elected = 0;
      summarized_local_state.counting = 0;
      summarized_local_state.unknown = 0;

      Object.keys(localcodes).forEach(localcode => {
        let sum_of_local_seat_polls = null;

        const polls = local_seat_polls[localcode];
        if(Array.isArray(polls) && polls.length > 0) {
          // console.log(localcode, localSeatPoll);
          sum_of_local_seat_polls = {};
          polls.forEach(poll => {
            const respondent = poll.respondent;   // 
            // const pollSummary2 = {};
            if(respondent > 0 && Array.isArray(poll.data) && poll.data.length > 0) {
              // console.log(localcode, polls);

              poll.data.forEach(item => {
                const {party, name, ratio} = item;
                const key = party + "-" + name;

                // console.log(party, name, ratio);
                if(ratio >= 0) {
                  const value = ratio * respondent;
                  if(key in sum_of_local_seat_polls) {
                    sum_of_local_seat_polls[key].ratio += ratio;
                    sum_of_local_seat_polls[key].count++;

                    // pollSummary2[key].ratio += value;
                    // pollSummary2[key].respondent += respondent;
                  } else {
                    sum_of_local_seat_polls[key] = {party, name, ratio : ratio, count : 1};
                    // pollSummary2[key] = {party, name, ratio : value, respondent};
                  }
                }
              });
            }
          });
        }

        if(sum_of_local_seat_polls) {
          // console.log(sum_of_local_seat_polls);
          // pollEncounter[localcode] = 0;
          local_seat_poll_summary[localcode] = [];
          
          Object.keys(sum_of_local_seat_polls).forEach(key => {
            const {party, name, ratio, count} = sum_of_local_seat_polls[key];
            const newratio = ratio / count;
            local_seat_poll_summary[localcode].push({party, name, ratio : parseFloat(newratio.toFixed(2))})
          });
        }


        //  
        let sum_of_local_seat_exitpolls = null;
        const exitpolls = local_seat_exitpoll[localcode];
        if(exitpolls) {
          // const exitpoll_array = [exitpolls.KBS, exitpolls.MBC, exitpolls.SBS, exitpolls.JTBC];
          const exitpoll_array = [exitpolls.KBS, exitpolls.MBC, exitpolls.SBS];

          sum_of_local_seat_exitpolls = {};
          exitpoll_array.forEach(exitpoll => {
            if(exitpoll) {
              exitpoll.forEach(item => {
                const {party, name, ratio} = item;
                const key = party + "-" + name;

                // console.log(party, name, ratio);
                if(ratio >= 0) {
                  if(key in sum_of_local_seat_exitpolls) {
                    sum_of_local_seat_exitpolls[key].ratio += ratio;
                    sum_of_local_seat_exitpolls[key].count++;
                  } else {
                    sum_of_local_seat_exitpolls[key] = {party, name, ratio : ratio, count : 1};
                  }
                }
              });
            }
          });

          if(sum_of_local_seat_exitpolls) {
            // console.log(sum_of_local_seat_exitpolls);
            // pollEncounter[localcode] = 0;
            local_seat_exitpoll_summary[localcode] = [];
            
            Object.keys(sum_of_local_seat_exitpolls).forEach(key => {
              const {party, name, ratio, count} = sum_of_local_seat_exitpolls[key];
              const newratio = ratio / count;
              local_seat_exitpoll_summary[localcode].push({party, name, ratio : parseFloat(newratio.toFixed(2))})
            });
          }
          // if(localcode === "2110101") {
          //   // console.log(exitpoll_array, local_seat_exitpoll_summary[localcode]);
          //   console.log(local_seat_exitpoll_summary[localcode]);
          // }
        }




        // if(localcode === "2110101") {
          let party = "";
          const item = {};
          item.candidate = local_seat_candidate[localcode];
          item.polls = local_seat_polls[localcode];
          item.exitpolls = local_seat_exitpoll[localcode];

          // 
          let local_encounter = 0;
          if(local_seat_poll_summary && localcode in local_seat_poll_summary) {
            const data = local_seat_poll_summary[localcode];
            const first = this.extendPartyInfo(this.find1st(data));
            const encounter = this.findPollEncounter(data);
            item.poll = {
              data,
              first,
              encounter
            };
            item.first = first;
            item.data = data;
            item.encounter = encounter;
            party = first.party;
            local_encounter = encounter;
          }

          // 
          if(local_seat_exitpoll_summary && localcode in local_seat_exitpoll_summary) {
            const data = local_seat_exitpoll_summary[localcode];
            const first = this.extendPartyInfo(this.find1st(data));
            const encounter = this.findPollEncounter(data);
            item.exitpoll = {
              data,
              first,
              encounter
            };
            item.first = first;
            item.data = data;
            item.encounter = encounter;
            party = first.party;
            local_encounter = encounter;
          }

          // 
          if(local_seat_final && localcode in local_seat_final) {
            const data = local_seat_final[localcode];
            const first = this.extendPartyInfo(this.find1st(data));
            const encounter = this.findPollEncounter(data);
            item.final = {
              data,
              first,
              encounter
            };
            item.first = first;
            item.data = data;
            item.encounter = encounter;
            party = first.party;
            local_encounter = encounter;

            let elected = null;
            local_seat_final[localcode].some(item => {
              if(item.elected) {
                elected = this.extendPartyInfo(item);
                return true;
              }
            });
            if(elected) {
              party = elected.party;
              item.elected = elected;
              item.first = elected;
            }
          }
          merged_local_seat_data[localcode] = item;

          // if(localcode === "2110101") {
          //   console.log(item);
          // }

          //  
          if(item.elected) {
            summarized_local_state.elected++;
          } else if(local_encounter > 0 && local_encounter <= __RATIO_VERY_ENCOUNTER) {
            summarized_local_state.veryencounter++;
          } else if(local_encounter > 0 && local_encounter <= __RATIO_ENCOUNTER) {
            summarized_local_state.encounter++;
          } else if(local_encounter > 0) {
            summarized_local_state.counting++;
          } else {
            summarized_local_state.unknown++;
          }


          //    
          if(party) {
            if(party in summarized_local_seat) {
              summarized_local_seat[party]++;
            } else {
              summarized_local_seat[party] = 1;
            }
            total++;
          }

          
          // local_encounter
        // }
      });

      summarized_local_seat["undecided"] = __NO_RATIO_SEAT - total;
      this.summarized_local_seat = summarized_local_seat;
      this.summarized_local_state = summarized_local_state;

      // console.log("   ", summarized_local_seat);

      // console.log("", merged_local_seat_data);
      this.merged_local_seat_data = merged_local_seat_data;
    },

    summaryProportionalSeatPoll() {
      const sum = {};
      this.proportional_seat_poll_list.forEach(item => {
          const respondent = item.respondent;   // 
          Object.keys(item).forEach(key => {
            if(key !== "requester" && key !== "research" && key !== "period" && key !== "respondent") {
              const ratio = item[key];
              if(ratio >= 0) {
                const value = ratio * respondent;
                if(key in sum) {
                  sum[key].ratio += value;
                  sum[key].respondent += respondent;
                } else {
                  sum[key] = {ratio : value, respondent};
                }
              }
            }
          })
      });
      
      const summary = {};
      // console.log("proportional_seat_poll_list Sum : ", sum);

      let respondent = 0;
      Object.keys(sum).forEach(key => {
        summary[key] = sum[key].ratio / sum[key].respondent;
        if(key === "undecided") {
          respondent = sum[key].respondent;
        }
      });

      // console.log("proportional_seat_poll_list Summary : ", summary);

      this.summarized_proportional_seat_poll = summary;
      this.summarized_proportional_seat_poll.respondent = respondent;
    },

    readLocalSeat(index) {
      if(this.input_party[index] && this.input_party[index].localseat) {
        return parseFloat(this.input_party[index].localseat);
      }
      return 0;
    },
    seatRatio(index, key) {
      return (parseFloat(this.input_party[index].localseat) + (this.proportional_seat30[key] || 0) + (this.proportional_seat17[key] || 0))/300 * 100;
    },
    redistribute(amount, redistributeRatio) {
        const ret = {};
        let ratioSum = 0;
        Object.keys(redistributeRatio).forEach(key => {
          const data = redistributeRatio[key];
          if(data) {
            // console.log(key + " => " + data);
            ratioSum += data;
          }
        });
        // console.log("  : " + ratioSum);


        //  1
        let step1sum = 0;
        let remains = {};
        const share_party_list = [];
        // console.log("1 ");
        Object.keys(redistributeRatio).forEach(key => {
          const data = redistributeRatio[key];
          if(data) {
            const redata = data * amount / ratioSum;
            const relocalseat = Math.floor(redata);

            // remains[key] = redata - relocalseat;
            // console.log("\t", this.partyList[key].name, relocalseat + " => " + remains[key]);

            // console.log("\t", this.partyList[key].name, relocalseat);
            share_party_list.push({key:key, value: redata - relocalseat});

            ret[key] = relocalseat;
            step1sum += relocalseat;
          } else {
            ret[key] = 0;
          }
        });

        const rest_localseat = amount-step1sum;
        // console.log("1   : " + step1sum);
        // console.log("1     : " + rest_localseat);

        const filled = this.fillRest(share_party_list, rest_localseat);
        Object.keys(filled).forEach(key => {
          ret[key] += filled[key];
        });

        return ret;

    },
    fillRest(share_party_list, amount) {
      share_party_list.sort(function(a, b) {
        // 
        return b.value - a.value;
        // 44, 25, 21, 13
      });

      // console.log("  ");
      // share_party_list.forEach(party => {
      //   console.log("\t", this.partyList[party.key].name, party.value);
      // });

      var share_party_count = share_party_list.length;
      // console.log("  :", share_party_count);

      const second_share = {};
      for (var i = 1; i < amount + 1; i++) {
        let temp = i % share_party_count;
        temp = (temp == 0) ? share_party_count : temp;

        const party_index = temp - 1;
        const party = share_party_list[party_index].key;

        // console.log(" : ", party_index, JSON.stringify(share_party_list[party_index]))
        if(second_share[party]) {
          second_share[party]++;
        } else {
          second_share[party] = 1;
        }
        //console.log(temp, share_party_list[party_key].party);
      }
      // console.log(" : " + JSON.stringify(second_share))
      return second_share;
    },
    firstCome(list, amount) {
      if(amount <= 0) return [];

      let firstParty = "";
      let partyValue = 0;

      Object.keys(list).forEach(key=> {
        if(partyValue <= list[key]){
          firstParty = key;
          partyValue = list[key];
        }
      });

      const ret = [];
      ret.push(firstParty);
      if(amount > 1) {
        delete list[firstParty];
        const nextParty = this.firstCome(list, amount - 1);
        ret.push(...nextParty);
      }

      return ret;
    },

    selectProportionalSeatPoll(poll, ignoreCalcuate) {
      // summarized_proportional_seat_poll
      // selected_proportional_seat_poll
      // console.log("selectProportionalSeatPoll", poll);
      this.selected_proportional_seat_poll = poll;

      this.partyKey.forEach((key, index) => {
        // console.log(key, index);
        if(poll.hasOwnProperty(key) && poll[key] > 0) {
          const ratio = parseFloat(poll[key]) || 0;
          this.input_party[index].ratio = ratio.toFixed(2);
        } else {
          this.input_party[index].ratio = 0;
        }
      });

      if(!ignoreCalcuate) this.calculate();
    },
    
    selectLocalSeatSample(sample) {
      // console.log(" ", sample);

      const local_seat_ratio = {};
      Object.keys(this.partyList).forEach(key => {
        const party = this.partyList[key];
        const data = sample[key];

        if(data) {
          // console.log("\t", party.name, data);
          //     
          if(data && !party.localLock) {
            local_seat_ratio[key] = data;
          }
        }
      });

      const redistribute_local_seats = this.redistribute(__NO_RATIO_SEAT, local_seat_ratio);

      // console.log(" ");
      this.partyKey.forEach((key, index) => {
        // const party = this.partyList[key];
        const seats = (key in redistribute_local_seats) ? redistribute_local_seats[key] : 0;
        if(this.input_party[index]) {
          this.input_party[index].localseat = seats;
        }
        // console.log("\t", this.partyList[key].name, seats);
      });

      this.calculate();
    },

    calculate() {
      // console.log("....................calculate....................");
      // console.log(" ");
      let totalLocalSeat = 0;
      this.partyKey.forEach((key, index) => {
        const party = this.partyList[key];
        // this.selectedPoll.data[key] = this.selectedPoll.data[key] || {localseat:0, ratio: 0};
        const localseat = parseFloat(this.input_party[index].localseat) || 0;
        const ratio = parseFloat(this.input_party[index].ratio) || 0;

        totalLocalSeat += localseat;
        // console.log("\t", this.partyList[key].name, localseat, ratio);
      });
      this.input_localseat_total = totalLocalSeat;



      let baseSeat = __TOTAL_SEAT;
      const ratioSeatPartyList = [];
      let ratioSum = 0;
      // Object.keys(this.selectedPoll.data).forEach(key => {
      //   const data = this.selectedPoll.data[key];
      //   ratioSum += data.ratio;
      // });
      this.partyKey.forEach((key, index) => {
        const ratio = parseFloat(this.input_party[index].ratio) || 0;
        const localSeat = parseFloat(this.input_party[index].localseat) || 0;
        if(key !== "undecided" && key !== 'etcparty' && key !== 'noparty' && (ratio >= 3 || localSeat >= 5)) {
          ratioSum += parseFloat(ratio.toFixed(1));
          ratioSeatPartyList.push(key);
        } else {
          baseSeat -= localSeat;
        }
      });
      this.input_ratio_total = ratioSum.toFixed(1);
      // console.log(" :", ratioSeatPartyList);
      // console.log("  : " + baseSeat);
      // console.log("   : " + ratioSum);






      let rescaled_ratio_sum = 0;
      // console.log("  ");
      this.partyKey.forEach((key, index) => {
        //const party = this.partyList[key];
        const ratio = parseFloat(this.input_party[index].ratio) || 0;
        const localSeat = parseFloat(this.input_party[index].localseat) || 0;
        const reratio = parseFloat(ratio * 100 / ratioSum.toFixed(1));
        // delete this.ratio100[key];

        if(key !== "undecided" && key !== 'etcparty' && key !== 'noparty' && (ratio >= 3 || localSeat >= 5)) {
          this.ratio100[key] = reratio;
          rescaled_ratio_sum += reratio;
        } else {
          this.ratio100[key] = 0;
        }
        // console.log("\t", this.partyList[key].name, this.ratio100[key]);
      });
      
      // this.input_ratio100_total = rescaled_ratio_sum.toFixed(1);
      // console.log("    100%  : " + rescaled_ratio_sum);








      // // extraSeat += this.selectedPoll.data['noparty'].localseat;
      // const minus_seat_table = {};
      // const ratioSeatPartyList1 = [];
      // // console.log("     ");
      // this.partyKey.forEach((key, index) => {
      //   const party = this.partyList[key];
      //   const ratio = this.ratio100[key] || 0;
      //   // const localSeat = this.selectedPoll.data[key].localseat || 0;
      //   const localSeat = parseFloat(this.input_party[index].localseat) || 0;

      //   //  0 (and),  ( 3%   or   5  )      (ex_list) 
      //   // ,  3%  and   5       , (base_localseat) 
      //   // 3%  5  
      //   if(key === 'noparty') {
      //     // extraSeat += this.selectedPoll.data[key].localseat;
      //       minus_seat_table[key] = localSeat;
      //   } else if(ratio >= 3 || localSeat >= 5) {
      //     if(key !== "etcparty") ratioSeatPartyList1.push(key);
      //     if(ratio > 0) {
      //       minus_seat_table[key] = 0;
      //     } else {
      //       minus_seat_table[key] = localSeat;
      //     }
      //   } else {
      //     // minus_seat_table[key] = this.selectedPoll.data[key].localseat;
      //     minus_seat_table[key] = localSeat;
      //   }
      //   // console.log("\t", party.name, ratio, minus_seat_table[key]);
      //   baseSeat -= minus_seat_table[key];
      // });
      
      // delete ratioSeatPartyList.etcparty;
      // // console.log("1.  :", baseSeat);
      // // console.log(" :", ratioSeatPartyList);





      // console.log("  :", __RATIO_SEAT_NO_CAP);
      const ratio100 = JSON.parse(JSON.stringify(this.ratio100));
      Object.keys(ratio100).forEach(key => {
        if(!ratioSeatPartyList.includes(key)) {
          delete ratio100[key];
        }
      });
      delete ratio100["etcparty"];
      this.ratio100 = ratio100;
      // console.log("  :", ratio100);





      this.proportional_seat17 = {};
      let proportional_seat17sum = 0;
      const proportional_seat17 = this.redistribute(__RATIO_SEAT_NO_CAP, ratio100);
      Object.keys(proportional_seat17).forEach(key => {
        this.proportional_seat17[key] = proportional_seat17[key];
        proportional_seat17sum += proportional_seat17[key];
        // console.log("\t", this.partyList[key].name, this.proportional_seat17[key]);
      });
      this.input_proportional_seat17_total = proportional_seat17sum;
      // console.log("  :", JSON.stringify(this.proportional_seat17));





      let first_share_total = 0;
      const first_share = {};
      const first_share_base_num = {};
      // console.log("2. ")
      this.partyKey.forEach((key, index) => {
        const party = this.partyList[key];
        // const localSeat = this.selectedPoll.data[key].localseat || 0;
        const localSeat = parseFloat(this.input_party[index].localseat) || 0;
        if(ratioSeatPartyList.includes(key) && key !== 'party' && key !== 'noparty' && key !== 'undecided') {
          const fsbn = ((baseSeat * this.ratio100[key]) / 100 - localSeat) / 2;

          first_share_base_num[key] = fsbn;

          // 3. 1    first_share_i
          //      
          first_share[key] = 0;
          if (Math.round(fsbn) < 1) {
            first_share[key] = 0;
          } else {
            first_share[key] = Math.round(fsbn);
            first_share_total += first_share[key];
            // console.log("\t", party.name, first_share[key]);
          }
        }
      });
      
      // console.log("   :", first_share_party_list);
      // console.log("1  :", first_share_total);
      // console.log(
      //   "3.  1 ():", JSON.stringify(first_share),
      //   ":", first_share_total
      // );





      // first_share_total = 35;
      if(first_share_total === __RATIO_SEAT_CAP) {
        // console.log(" ");
        this.partyKey.forEach((key, index) => {
          this.proportional_seat30[key] = first_share[key] || 0;
          // console.log("\t", this.partyList[key].name, this.proportional_seat30[key] || 0);
        });
      } else if (first_share_total > __RATIO_SEAT_CAP) {
        // console.log("1  30   ");
        //30     1   
        //1.     

        // console.log("1    ")
        const first_share = {};
        let new_first_share_total = 0;
        let share_party_list = [];
        this.partyKey.forEach((key, index) => {
          if(ratioSeatPartyList.includes(key) && key !== 'party' && key !== 'noparty' && key !== 'undecided') {
            const party = this.partyList[key];
            if(first_share_base_num[key] && first_share_base_num[key] > 0) {
              const fsbn = first_share_base_num[key];
              const num = (__RATIO_SEAT_CAP * fsbn) / first_share_total;
              const localseat = Math.floor(num);

              first_share[key] = localseat;
              share_party_list.push({key:key, value:num - localseat});

              new_first_share_total += localseat;

              // console.log("\t", party.name, num, localseat);
            }
          }
        });

        rest_localseat = __RATIO_SEAT_CAP - new_first_share_total;
        // console.log("  : ", rest_localseat)

        const second_share = this.fillRest(share_party_list, rest_localseat);

        // console.log("2 ");
        // Object.keys(second_share).forEach(key => {
        //   console.log("\t", this.partyList[key].name, second_share[key]);
        // });

        // console.log(" ");
        let seat30sum = 0;
        this.partyKey.forEach((key, index) => {
          this.proportional_seat30[key] = (first_share[key] || 0) + (second_share[key] || 0);
          seat30sum += this.proportional_seat30[key];
          // console.log("\t", this.partyList[key].name, this.proportional_seat30[key] || 0);
        });
        this.input_proportional_seat30_total = seat30sum;
      } else {
        //first_share_total 30 

        // console.log("1  30  ");
        var rest_localseat = __RATIO_SEAT_CAP - first_share_total; // 1    -   
        //ex_list2    

        // console.log("1    : ", rest_localseat);
        //1    ex) rest_localseat =6

        const second_share = this.redistribute(rest_localseat, this.ratio100);

        // console.log("1  (+):", JSON.stringify(first_share));
        // console.log("2  (+):", JSON.stringify(second_share));
        
        // console.log(" ");
        let seat30sum = 0;
        this.partyKey.forEach((key, index) => {
          if(ratioSeatPartyList.includes(key) && key !== 'party' && key !== 'noparty' && key !== 'undecided') {
            const party = this.partyList[key];
            this.proportional_seat30[key] = (first_share[key] || 0) + (second_share[key] || 0);
            seat30sum += this.proportional_seat30[key];
            // console.log("\t", this.partyList[key].name, this.proportional_seat30[key] || 0);
          }
        });
        this.input_proportional_seat30_total = seat30sum;
      }


      console.log(" ");
      let seatTotal = 0;
      const party_chart_label = [];
      const party_chart_color = [];
      const party_chart_data = [];

      const party_group_dataset = {};
      this.partyKey.forEach((key, index) => {
        const party = this.partyList[key];
        let num = (this.readLocalSeat(index) + (this.proportional_seat30[key] || 0) + (this.proportional_seat17[key] || 0));
        seatTotal += num || 0;

        // console.log("\t", party.name, this.readLocalSeat(index), this.proportional_seat30[key], this.proportional_seat17[key]);
        console.log("\t", party.name, num || 0);

        party_chart_label.push(party.name);
        party_chart_color.push(party.color);
        party_chart_data.push(num || 0);

        if(party_group_dataset[party.group]) {
          party_group_dataset[party.group] += num || 0;
        } else {
          party_group_dataset[party.group] = num || 0;
        }
      });
      this.input_seat_total = seatTotal;

      this.chartData.labels = party_chart_label;
      this.chartData.datasets[0].backgroundColor = party_chart_color;
      this.chartData.datasets[0].data = party_chart_data;


      const party_group_label = [];
      const party_group_color = [];
      const party_group_data = [];
      Object.keys(party_group_dataset).forEach(key => {
        const group = this.partyGroup[key];
        party_group_label.push(group.name);
        party_group_color.push(group.color);
        party_group_data.push(party_group_dataset[key]);
      });

      this.groupData.labels = party_group_label;
      this.groupData.datasets[0].backgroundColor = party_group_color;
      this.groupData.datasets[0].data = party_group_data;

      this.updateChart();
    },

    updateChart () {
      this.$refs.party_group_chart.update();
      this.$refs.party_seat_chart.update();
    },

    goLink(url) {
      window.open(url, "_blank");
    },



    find1st(data) {
      const cloned = JSON.parse(JSON.stringify(data));
      if(cloned && Array.isArray(cloned) && cloned.length > 0) {
        // console.log(zone);
        let candidate1st = "";
        let maxRatio = 0;
        
        cloned.forEach(item => {
          if(maxRatio < parseFloat(item.ratio)) {
            maxRatio = parseFloat(item.ratio);
            candidate1st = item;
          }
        });
        
        return candidate1st;
      }
      return null;
    },
    findPollEncounter(data) {
      // console.log("findPollEncounter", zone);
      // const zone = poll["" + code];
      const cloned = JSON.parse(JSON.stringify(data));
      if(cloned && Array.isArray(cloned) && cloned.length > 0) {
          cloned.sort(function(a, b) {
            // 
            return b.ratio - a.ratio;
            // 44, 25, 21, 13
          });

          if(cloned.length >= 2) {
            return cloned[0].ratio - cloned[1].ratio;
          }
      }
      return 0;
    },
  }
}
</script>

